import Register from '@/pages/Register'
import Login from '@/pages/Login'
import Home  from '@/pages/Home'

export default [

    /**
     * 
     */
    {
        path: '/login',
        name:'Login',
        component: Login,
        meta: { requiresGuest: true }, // 
    },

    /**
     * 
     */
    {
        path: '/register',
        name:'Register',
        component: Register,
        meta: { requiresGuest: true }, // 
    },



    /**
     * 
     */
    {
        path: '/',
        name:'Home',
        component: Home,
        meta: { requiresAuth: true }, // 
    }
]